<template>
	<div class="edit-password">
		<div style="background: white;">
			<div v-if="showBefore">
				<p style="padding: 20px 0 0 5%;margin: 0;">请输入原密码</p>
				<div class="input-focus">
					<input @input="handleInput1" v-model="password1" ref="beforePsd" type="number" v-focus/>
				</div>
				<mu-flex @click="password1GetFocus" class="flex-wrapper" style="position: relative;" align-items="center">
					<mu-flex class="flex-demo" justify-content="center">
						<p v-if="password1.length>0" style="width: 10px;height: 10px;margin:auto;border-radius: 5px;background: #295DF1;"></p>
					</mu-flex>
					<mu-flex class="flex-demo" justify-content="center">
						<p v-if="password1.length>1" style="width: 10px;height: 10px;margin:auto;border-radius: 5px;background: #295DF1;"></p>
					</mu-flex>
					<mu-flex class="flex-demo" justify-content="center">
						<p v-if="password1.length>2" style="width: 10px;height: 10px;margin:auto;border-radius: 5px;background: #295DF1;"></p>
					</mu-flex>
					<mu-flex class="flex-demo" justify-content="center">
						<p v-if="password1.length>3" style="width: 10px;height: 10px;margin:auto;border-radius: 5px;background: #295DF1;"></p>
					</mu-flex>
					<mu-flex class="flex-demo" justify-content="center">
						<p v-if="password1.length>4" style="width: 10px;height: 10px;margin:auto;border-radius: 5px;background: #295DF1;"></p>
					</mu-flex>
					<mu-flex class="flex-demo" justify-content="center">
						<p v-if="password1.length>5" style="width: 10px;height: 10px;margin:auto;border-radius: 5px;background: #295DF1;"></p>
					</mu-flex>
				</mu-flex>
				<!--<p style="color: #CEBC98;margin: 50px;text-align: center;"> 忘记密码 </p>-->
			</div>
			<div v-if="showSet">
				<p style="padding: 20px 0 0 5%;margin: 0;">请设置新的密码</p>
				<div class="input-focus">
					<input @input="handleInput2" ref="afterPsd" v-model="password2" type="number" />
				</div>
				<mu-flex @click="password2GetFocus" class="flex-wrapper" style="position: relative;" align-items="center">
					<mu-flex class="flex-demo" justify-content="center">
						<p v-if="password2.length>0" style="width: 10px;height: 10px;margin:auto;border-radius: 5px;background: #295DF1;"></p>
					</mu-flex>
					<mu-flex class="flex-demo" justify-content="center">
						<p v-if="password2.length>1" style="width: 10px;height: 10px;margin:auto;border-radius: 5px;background: #295DF1;"></p>
					</mu-flex>
					<mu-flex class="flex-demo" justify-content="center">
						<p v-if="password2.length>2" style="width: 10px;height: 10px;margin:auto;border-radius: 5px;background: #295DF1;"></p>
					</mu-flex>
					<mu-flex class="flex-demo" justify-content="center">
						<p v-if="password2.length>3" style="width: 10px;height: 10px;margin:auto;border-radius: 5px;background: #295DF1;"></p>
					</mu-flex>
					<mu-flex class="flex-demo" justify-content="center">
						<p v-if="password2.length>4" style="width: 10px;height: 10px;margin:auto;border-radius: 5px;background: #295DF1;"></p>
					</mu-flex>
					<mu-flex class="flex-demo" justify-content="center">
						<p v-if="password2.length>5" style="width: 10px;height: 10px;margin:auto;border-radius: 5px;background: #295DF1;"></p>
					</mu-flex>
				</mu-flex>
				<!--<div style="position: absolute;top: 110px;left:5%;background: #e5e5e5;height: 45px;width: 100%;">
				<div class="input">
				  </div>
			</div>-->
			</div>

			<div v-if="showConfirm">
				<p style="padding: 20px 0 0 5%;margin: 0;">请确认新的密码</p>
				<div class="input-focus">
					<input @input="handleInput3" ref="confirmPsd" v-model="password3" type="number" />
				</div>
				<mu-flex @click="password3GetFocus" class="flex-wrapper" style="position: relative;" align-items="center">
					<mu-flex class="flex-demo" justify-content="center">
						<p v-if="password3.length>0" style="width: 10px;height: 10px;margin:auto;border-radius: 5px;background: #295DF1;"></p>
					</mu-flex>
					<mu-flex class="flex-demo" justify-content="center">
						<p v-if="password3.length>1" style="width: 10px;height: 10px;margin:auto;border-radius: 5px;background: #295DF1;"></p>
					</mu-flex>
					<mu-flex class="flex-demo" justify-content="center">
						<p v-if="password3.length>2" style="width: 10px;height: 10px;margin:auto;border-radius: 5px;background: #295DF1;"></p>
					</mu-flex>
					<mu-flex class="flex-demo" justify-content="center">
						<p v-if="password3.length>3" style="width: 10px;height: 10px;margin:auto;border-radius: 5px;background: #295DF1;"></p>
					</mu-flex>
					<mu-flex class="flex-demo" justify-content="center">
						<p v-if="password3.length>4" style="width: 10px;height: 10px;margin:auto;border-radius: 5px;background: #295DF1;"></p>
					</mu-flex>
					<mu-flex class="flex-demo" justify-content="center">
						<p v-if="password3.length>5" style="width: 10px;height: 10px;margin:auto;border-radius: 5px;background: #295DF1;"></p>
					</mu-flex>
				</mu-flex>
				<!--<div style="position: absolute;top: 110px;left:5%;background: #e5e5e5;height: 45px;width: 100%;">
				<div class="input">
				  </div>
			</div>-->
			</div>

		</div>
	</div>
</template>

<script>
	export default {
		name: "",
		data() {
			return {
				password1: '',
				password2: '',
				password3: '',
				showBefore: true,
				showSet: false,
				showConfirm: false
			};
		},
		created() {
			this.walletInfo = JSON.parse(this.$route.query.details);
		},
		methods: {
			password1GetFocus() {
				this.$refs.beforePsd.focus();
			},
			password2GetFocus() {
				this.$refs.afterPsd.focus();
			},
			password3GetFocus() {
				this.$refs.confirmPsd.focus();
			},
			//密码限制输入数字
			handleInput1(e) {
				this.password1 = e.target.value.replace(/[^\d]/g, '');
			},
			//重置密码限制输入数字
			handleInput2(e) {
				this.password2 = e.target.value.replace(/[^\d]/g, '');
			},
			//确认密码限制输入数字
			handleInput3(e) {
				this.password3 = e.target.value.replace(/[^\d]/g, '');
			}
		},
		mounted() {
			//			this.$refs.beforePsd.focus();
		},
		watch: {
			$route(to, form) {
				if(form.path == "/myWallet" && to.path == '/editPassword') {
					this.showBefore = true;
					this.showSet = false;
					this.showConfirm = false;
					this.password1 = '';
					this.password2 = '';
					this.password3 = '';
				}
			},
			password1: function(newV, oldV) {
				if(newV.length == 6) {
					if(this.walletInfo.pwd == newV) {
						this.showBefore = false;
						this.showSet = true;
						this.showConfirm = false;
					} else {
						this.$layer.open({
							content: '密码错误',
							skin: 'msg',
							time: 2 //2秒后自动关闭
						})
						this.password1 = '';
					}

				}
			},
			password2: function(newV, oldV) {
				if(newV.length == 6) {
					this.showBefore = false;
					this.showSet = false;
					this.showConfirm = true;
				}
			},
			password3: function(newV, oldV) {
				if(newV.length == 6) {
					if( this.password2 == newV){
						let localStorageArr = JSON.parse(localStorage.getItem('walletArr'));
						for(let i in localStorageArr){
							if(localStorageArr[i].secretSeed == this.walletInfo.secretSeed){
								this.$set(localStorageArr[i],'pwd',newV)
								localStorage.setItem('walletArr',JSON.stringify(localStorageArr))
								this.$router.replace('/walletManage');
							}
						}

					}else{
						this.$layer.open({
							content: '两次密码输入不一致',
							skin: 'msg',
							time: 2 //2秒后自动关闭
						})
						this.password3=''
					}
				}
			}

		}
	};
</script>

<style scoped>
	.edit-password {
		overflow-y: scroll;
	}
	/*密码框 样式*/
	
	.flex-wrapper {
		height: 56px;
		margin: 25px;
	}
	
	.flex-demo {
		width: 50px;
		height: 38px;
		background-color: white;
		text-align: center;
		line-height: 38px;
		margin-left: 15px;
	}
	
	.flex-demo:first-child {
		margin-left: 0;
	}
</style>


